export default class Conocimiento {
  constructor(conocimiento_id, material_id, preguntas, respuestas) {
    this.conocimiento_id = conocimiento_id;
    this.material_id = material_id;
    this.material_nombre = "";
    this.preguntas = preguntas;
    this.respuestas = respuestas;
    this._mostrarDetalle = false;
  }
}