<template>
  <v-card elevation="0" style="background: #f9ceff">
    <img
      style="display:block; margin: 0 auto; max-width: 200px"
      :src="src"
      @error="errorImage()"
      aspect-ratio="1"
    />
    <v-speed-dial v-model="fab" absolute bottom right direction="left">
      <template v-slot:activator>
        <v-btn v-model="fab" color="blue darken-2" small dark fab>
          <v-icon v-if="fab">mdi-close</v-icon>
          <v-icon v-else>mdi-drama-masks</v-icon>
        </v-btn>
      </template>
      <v-btn fab dark small color="green" @click="showEmotion('HAP', emotion_time)">
        <v-icon>mdi-emoticon-happy-outline</v-icon>
      </v-btn>
      <v-btn fab dark small color="indigo" @click="showEmotion('SAD', emotion_time)">
        <v-icon>mdi-emoticon-sad-outline</v-icon>
      </v-btn>
      <v-btn fab dark small color="red" @click="showEmotion('ANG', emotion_time)">
        <v-icon>mdi-emoticon-angry-outline</v-icon>
      </v-btn>
    </v-speed-dial>
  </v-card>
</template>

<script>
export default {
  data() {
    return {
      src: "",
      emotion_time: 1000,
      animation_timeout: null,
      local_images: true,
      fab: false,
      allAvatarStyle: ["Circle", "Transparent"],
      allTopType: [
        "NoHair",
        "Eyepatch",
        "Hat",
        "Hijab",
        "Turban",
        "WinterHat1",
        "WinterHat2",
        "WinterHat3",
        "WinterHat4",
        "LongHairBigHair",
        "LongHairBob",
        "LongHairBun",
        "LongHairCurly",
        "LongHairCurvy",
        "LongHairDreads",
        "LongHairFrida",
        "LongHairFro",
        "LongHairFroBand",
        "LongHairNotTooLong",
        "LongHairShavedSides",
        "LongHairMiaWallace",
        "LongHairStraight",
        "LongHairStraight2",
        "LongHairStraightStrand",
        "ShortHairDreads01",
        "ShortHairDreads02",
        "ShortHairFrizzle",
        "ShortHairShaggyMullet",
        "ShortHairShortCurly",
        "ShortHairShortFlat",
        "ShortHairShortRound",
        "ShortHairShortWaved",
        "ShortHairSides",
        "ShortHairTheCaesar",
        "ShortHairTheCaesarSidePart"
      ],
      allAccessoriesType: [
        "Blank",
        "Kurt",
        "Prescription01",
        "Prescription02",
        "Round",
        "Sunglasses",
        "Wayfarers"
      ],
      allHairColor: [
        "Auburn",
        "Black",
        "Blonde",
        "BlondeGolden",
        "Brown",
        "BrownDark",
        "PastelPink",
        "Platinum",
        "Red",
        "SilverGray"
      ],
      allFacialHairType: [
        "Blank",
        "BeardMedium",
        "BeardLight",
        "BeardMagestic",
        "MoustacheFancy",
        "MoustacheMagnum"
      ],
      allClotheType: [
        "BlazerShirt",
        "BlazerSweater",
        "CollarSweater",
        "GraphicShirt",
        "Hoodie",
        "Overall",
        "ShirtCrewNeck",
        "ShirtScoopNeck",
        "ShirtVNeck"
      ],
      allEyeType: [
        "Close",
        "Cry",
        "Default",
        "Dizzy",
        "EyeRoll",
        "Happy",
        "Hearts",
        "Side",
        "Squint",
        "Surprised",
        "Wink",
        "WinkWacky"
      ],
      allEyebrowType: [
        "Angry",
        "AngryNatural",
        "Default",
        "DefaultNatural",
        "FlatNatural",
        "RaisedExcited",
        "RaisedExcitedNatural",
        "SadConcerned",
        "SadConcernedNatural",
        "UnibrowNatural",
        "UpDown",
        "UpDownNatural"
      ],
      allMouthType: [
        "Concerned",
        "Default",
        "Disbelief",
        "Eating",
        "Grimace",
        "Sad",
        "ScreamOpen",
        "Serious",
        "Smile",
        "Tongue",
        "Twinkle",
        "Vomit"
      ],
      allSkinColor: ["Tanned", "Light", "Brown", "DarkBrown", "Black"],
      /**/
      avatarStyle: "",
      topType: "",
      accessoriesType: "",
      hairColor: "",
      facialHairType: "",
      clotheType: "",
      eyeType: "",
      eyebrowType: "",
      mouthType: "",
      skinColor: ""
    };
  },
  mounted() {
    this.setAvatar(0);
    this.setAnimationNormal();
    // this.prueba1();
  },
  methods: {
    errorImage() {
      this.setNormal();
    },
    setAvatar(idx) {
      let avatars = [
        {
          avatarStyle: "Transparent",
          topType: "LongHairStraightStrand",
          accessoriesType: "Blank",
          hairColor: "BrownDark",
          facialHairType: "Blank",
          clotheType: "BlazerShirt",
          skinColor: "Light"
        }
      ];
      this.avatarStyle = avatars[idx].avatarStyle;
      this.topType = avatars[idx].topType;
      this.accessoriesType = avatars[idx].accessoriesType;
      this.hairColor = avatars[idx].hairColor;
      this.facialHairType = avatars[idx].facialHairType;
      this.clotheType = avatars[idx].clotheType;
      this.skinColor = avatars[idx].skinColor;
    },
    showEmotion(emotion, time) {
      this.stopAnimation();
      if (emotion == "HAP") {
        this.setHappy();
      } else if (emotion == "SAD") {
        this.setSad();
      } else if (emotion == "ANG") {
        this.setAngry();
      } else if (emotion == "LOV") {
        this.setLove();
      }
      this.animation_timeout = setTimeout(() => {
        this.setAnimationNormal();
      }, time);
    },
    setAnimationNormal() {
      //Include Blink
      this.stopAnimation();
      this.startAnimation("normal", "blink", 3000, 250);
    },
    setAnimationTalk(time) {
      this.stopAnimation();
      this.startAnimation("talk", "normal", 200, 200);
      setTimeout(() => {
        this.stopAnimation();
        this.setAnimationNormal();
      }, time);
    },
    startAnimation(first, second, first_rate, second_rate) {
      this.src = require(`@/assets/avatar/${first}.png`);
      this.animation_timeout = setTimeout(() => {
        this.src = require(`@/assets/avatar/${second}.png`);
        this.startAnimation(second, first, second_rate, first_rate);
      }, first_rate);
    },
    stopAnimation() {
      clearTimeout(this.animation_timeout);
    },
    setNormal() {
      if (this.local_images) this.src = require("@/assets/avatar/normal.png");
      else {
        this.eyeType = "Default";
        this.eyebrowType = "DefaultNatural";
        this.mouthType = "Twinkle";
        this.updateAvatar();
      }
    },
    setHappy() {
      if (this.local_images) this.src = require("@/assets/avatar/happy.png");
      else {
        this.eyeType = "Happy";
        this.eyebrowType = "RaisedExcitedNatural";
        this.mouthType = "Smile";
        this.updateAvatar();
      }
    },
    setSad() {
      if (this.local_images) this.src = require("@/assets/avatar/sad.png");
      else {
        this.eyeType = "Cry";
        this.eyebrowType = "SadConcernedNatural";
        this.mouthType = "Sad";
        this.updateAvatar();
      }
    },
    setAngry() {
      if (this.local_images) this.src = require("@/assets/avatar/angry.png");
      else {
        this.eyeType = "Close";
        this.eyebrowType = "AngryNatural";
        this.mouthType = "Serious";
        this.updateAvatar();
      }
    },
    setLove() {
      if (this.local_images) this.src = require("@/assets/avatar/love.png");
      else {
      }
    },
    updateAvatar() {
      this.src =
        `https://avataaars.io/png?` +
        `avatarStyle=${this.avatarStyle}` +
        `&topType=${this.topType}` +
        `&accessoriesType=${this.accessoriesType}` +
        `&hairColor=${this.hairColor}` +
        `&facialHairType=${this.facialHairType}` +
        `&clotheType=${this.clotheType}` +
        `&eyeType=${this.eyeType}` +
        `&eyebrowType=${this.eyebrowType}` +
        `&mouthType=${this.mouthType}` +
        `&skinColor=${this.skinColor}`;
    }
  }
};
</script>

<style lang="scss" scoped>
</style>