<template>
  <v-container fluid class="py-0 px-4">
    <v-row class="fill-height">
      <v-col cols="12" md="5" lg="4" class="fill-height">
        <v-card class="elevation-6" height="100%">
          <div class="chat-componente" style="height: 100%">
            <Avatar ref="avatar" />
            <v-container id="chat-ventana" class="contenedor-pattern-10" style="height: 100%">
              <div
                v-for="(mensaje, index) in mensajes"
                :key="index"
                :class="{'justify-end':mensaje.autor==1 }"
              >
                <v-card
                  v-if="mensaje.mensaje"
                  v-ripple
                  class="chat-mensaje elevation-3"
                  :class="{ 'chat-mensaje-0':mensaje.autor==0, 'chat-mensaje-1':mensaje.autor==1 }"
                >
                  <span>{{mensaje.mensaje}}</span>
                </v-card>
              </div>
              <div v-if="!enviar_mensaje">
                <v-card v-ripple class="chat-mensaje chat-mensaje-0 elevation-3" width="55">
                  <v-progress-linear buffer-value="0" stream color="primary" class="mt-3 mb-1"></v-progress-linear>
                </v-card>
              </div>
            </v-container>
            <div>
              <v-form class="chat-botones" @submit.prevent="enviarMensaje">
                <v-text-field v-model="texto" class="mr-3" autocomplete="off"></v-text-field>
                <v-btn
                  :loading="!enviar_mensaje"
                  fab
                  icon
                  dark
                  small
                  color="primary"
                  @click="enviarMensaje"
                >
                  <v-icon dark>mdi-send</v-icon>
                </v-btn>

                <v-btn
                  :loading="microfono_loading"
                  fab
                  icon
                  dark
                  small
                  color="primary"
                  @click="hablarMensaje()"
                >
                  <v-icon dark>mdi-microphone</v-icon>
                </v-btn>

                <v-menu offset-y>
                  <template v-slot:activator="{ on }">
                    <v-btn fab icon dark small color="primary" v-on="on">
                      <v-icon dark>mdi-help-circle-outline</v-icon>
                    </v-btn>
                  </template>
                  <div class="contenedor-preguntas-disponibles">
                    <p>Preguntas disponibles ({{conocimientos.length}})</p>
                    <div>
                      <v-list>
                        <v-list-item
                          v-for="(conocimiento, c_idx) in conocimientos"
                          :key="c_idx"
                          @click="escribirMensaje(conocimiento.preguntas[0]); if(!conocimiento.preguntas[0].includes('@')) enviarMensaje()"
                        >
                          <v-list-item-title>{{ conocimiento.preguntas[0] }}</v-list-item-title>
                        </v-list-item>
                      </v-list>
                    </div>
                  </div>
                </v-menu>
              </v-form>
            </div>
          </div>
        </v-card>
      </v-col>
      <v-col cols="12" md="7" lg="8" class="fill-height">
        <v-tabs v-model="tab" background-color="primary" dark>
          <v-tab>Material</v-tab>
          <v-tab>Cuestionario</v-tab>
        </v-tabs>

        <v-tabs-items v-model="tab" style="height: calc(100% - 23px)">
          <v-tab-item style="height: 100%">
            <v-card class="elevation-6 fill-height">
              <div v-if="material_seleccionado" class="fill-height">
                <v-toolbar dense flat color="#fff">
                  <v-btn icon @click="material_seleccionado = ''">
                    <v-icon>mdi-arrow-left</v-icon>
                  </v-btn>
                  <v-toolbar-title>{{material_seleccionado.nombre}}</v-toolbar-title>
                </v-toolbar>
                <div
                  class="contenedor-material-item d-flex flex-column"
                  style="height: calc(100% - 48px)"
                >
                  <div
                    v-if="prioridad.length > 1"
                    class="d-flex justify-space-between mt-3 mx-5"
                    style="position:relative"
                  >
                    <v-btn
                      class="primary"
                      v-if="item_seleccionado_idx != 0"
                      @click="seleccionarItem(item_seleccionado_idx - 1)"
                      style="z-index:1"
                    >Anterior</v-btn>
                    <div></div>
                    <div
                      class="material-item-index pt-2"
                    >{{item_seleccionado_idx+1}}/{{prioridad.length}}</div>
                    <v-btn
                      class="primary"
                      v-if="item_seleccionado_idx < prioridad.length - 1"
                      @click="seleccionarItem(item_seleccionado_idx + 1)"
                    >Siguiente</v-btn>
                  </div>
                  <div class="overflow-y-auto" style="height: calc(100% - 36px)">
                    <div
                      class="material-item"
                      v-if="item_seleccionado == 'texto' && material_seleccionado.texto"
                    >
                      <span class="material-item-titulo">Definición</span>
                      <span>{{material_seleccionado.texto}}</span>
                    </div>
                    <div
                      class="material-item"
                      v-if="item_seleccionado == 'importancia' && material_seleccionado.importancia"
                    >
                      <span class="material-item-titulo">Importancia</span>
                      <span>{{material_seleccionado.importancia}}</span>
                    </div>
                    <div
                      class="material-item"
                      v-if="item_seleccionado == 'ejemplos' && material_seleccionado.ejemplos && material_seleccionado.ejemplos.length > 0"
                    >
                      <span class="material-item-titulo">Ejemplos</span>
                      <ul class="mt-1">
                        <li
                          class="my-6"
                          v-for="(ejemplo, e_idx) in material_seleccionado.ejemplos"
                          :key="e_idx"
                        >{{ejemplo}}</li>
                      </ul>
                    </div>
                    <div
                      class="material-item"
                      v-if="item_seleccionado == 'quiz' && material_seleccionado.quiz && material_seleccionado.quiz.length > 0"
                    >
                      <span class="material-item-titulo">Ejercicios</span>
                      <div
                        class="mb-6"
                        v-for="(quiz, q_idx) in material_seleccionado.quiz"
                        :key="q_idx"
                      >
                        <span class="faq-pregunta mb-4">{{quiz.enunciado}}</span>
                        <span
                          v-for="(alternativa, a_idx) in quiz.alternativas"
                          :key="a_idx"
                          class="quiz-alternativa"
                          :class="{'quiz-alternativa-correcta': quiz.mostrando_correcta && (quiz.correcta == a_idx)}"
                          @click="seleccionarAlternativaQuiz(q_idx, a_idx)"
                        >{{alternativa}}</span>
                      </div>
                    </div>
                    <div
                      class="material-item"
                      v-if="item_seleccionado == 'imagen' && material_seleccionado.imagen"
                    >
                      <div class="material-imagen">
                        <img :src="material_seleccionado.imagen" alt />
                      </div>
                    </div>
                    <div
                      class="material-item material-video"
                      v-if="item_seleccionado == 'video' && material_seleccionado.video"
                    >
                      <div class="aspect-ratio-video">
                        <iframe
                          class="aspect-ratio-item"
                          :src="material_seleccionado.video"
                          allowfullscreen
                          alt
                        />
                      </div>
                    </div>
                    <div
                      class="material-item material-documento"
                      v-if="item_seleccionado == 'documento' && material_seleccionado.documento"
                    >
                      <div class="aspect-ratio-documento">
                        <embed class="aspect-ratio-item" :src="material_seleccionado.documento" alt />
                      </div>
                    </div>
                    <div
                      class="material-item"
                      v-if="item_seleccionado == 'faq' && material_seleccionado.faq && material_seleccionado.faq.length > 0"
                    >
                      <span class="material-item-titulo">Preguntas frecuentes</span>
                      <div
                        class="mt-6"
                        v-for="(f, f_idx) in material_seleccionado.faq"
                        :key="f_idx"
                      >
                        <span class="faq-pregunta">{{f.pregunta}}</span>
                        <span class="faq-respuesta mt-2">{{f.respuesta}}</span>
                      </div>
                    </div>
                    <div
                      class="material-item"
                      v-if="item_seleccionado == 'explicacion' && material_seleccionado.explicacion && material_seleccionado.explicacion.length > 0"
                    >
                      <span class="material-item-titulo">Para más información</span>
                      <ul class="mt-1">
                        <li
                          class="my-4"
                          v-for="(e, e_idx) in material_seleccionado.explicacion"
                          :key="e_idx"
                        >
                          <a :href="e.enlace" target="_blank">{{e.nombre}}</a>
                        </li>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
              <div v-else class="px-3 overflow-y-auto">
                <v-container>
                  <v-row>
                    <v-col
                      cols="12"
                      md="6"
                      lg="4"
                      v-for="(material, m_idx) in materiales"
                      :key="m_idx"
                    >
                      <v-card
                        class="cartel cartel-blue"
                        v-ripple
                        dark
                        flat
                        @click="seleccionarMaterial(material['_id']['$oid'])"
                      >
                        <span>{{material.nombre}}</span>
                      </v-card>
                    </v-col>
                    <v-col v-if="materiales_loading" cols="12" sm="6">
                      <div class="cartel-loading text-center">
                        <v-progress-circular :size="30" color="primary" indeterminate></v-progress-circular>
                      </div>
                    </v-col>
                  </v-row>
                </v-container>
              </div>
            </v-card>
          </v-tab-item>
          <v-tab-item style="height: 100%">
            <div v-if="mostrando_puntaje" class="text-center elevation-3 fill-height">
              <v-container class="overflow-y-auto">
                <h2 class="pa-3">Puntaje</h2>
                <v-progress-circular
                  :rotate="-90"
                  :value="puntaje_aux/cuestionario_seleccionado.preguntas.length*100"
                  :size="150"
                  :width="15"
                  color="#4caf50"
                  style="font-size: 30px"
                  class="ma-3"
                >{{puntaje}}/{{cuestionario_seleccionado.preguntas.length}}</v-progress-circular>
                <br />
                <v-btn
                  @click="mostrando_puntaje = false; cuestionario_seleccionado = null;"
                  class="mt-3 mb-6"
                >Volver</v-btn>
              </v-container>
            </div>
            <div v-else-if="cuestionario_seleccionado" class="elevation-3 fill-height">
              <v-container class="overflow-y-auto">
                <div class="d-flex" style="position:relative">
                  <v-btn icon large style="z-index:1" @click="cuestionario_seleccionado = null">
                    <v-icon>mdi-arrow-left</v-icon>
                  </v-btn>
                  <div class="material-item-index pt-2">
                    <span>Tiempo restante: {{cuestionario_tiempo}}</span>
                    <span>Pregunta: {{p_idx+1}}/{{cuestionario_seleccionado.preguntas.length}}</span>
                  </div>
                </div>
                <v-row class="text-center">
                  <v-col>
                    <div
                      class="my-5 cuestionario-enunciado"
                    >{{cuestionario_seleccionado.preguntas[p_idx].enunciado}}</div>
                  </v-col>
                </v-row>
                <v-row class="text-center" align="center" justify="center">
                  <v-col
                    cols="12"
                    md="6"
                    v-for="(alternativa, idx_a) in cuestionario_seleccionado.preguntas[p_idx].alternativas"
                    :key="idx_a"
                  >
                    <div
                      class="d-flex flex-row align-center"
                      style="max-width: 500px; margin: auto"
                    >
                      <div
                        class="elevation-2 px-10 py-5 cuestionario-alternativa"
                        :class="{ 'cuestionario-alternativa-correcta': mostrando_correcta && (idx_a == cuestionario_seleccionado.preguntas[p_idx].correcta)}"
                        @click="seleccionarAlternativa(idx_a)"
                      >{{alternativa}}</div>
                    </div>
                  </v-col>
                </v-row>
              </v-container>
            </div>
            <div v-else class="elevation-6 fill-height">
              <v-container class="overflow-y-auto px-6" fluid>
                <v-row>
                  <v-col
                    cols="12"
                    sm="6"
                    lg="4"
                    v-for="(cuestionario, c_idx) in cuestionarios"
                    :key="c_idx"
                  >
                    <v-card
                      class="cartel cartel-yellow"
                      v-ripple
                      dark
                      flat
                      @click="c_tab = 0; seleccionarCuestionario(c_idx)"
                    >
                      <span>{{cuestionario.nombre}}</span>
                      <span class="cartel-floating">{{cuestionario['nivel']}}</span>
                      <div class="mt-2">
                        <v-progress-circular
                          :size="50"
                          :value="cuestionario.evaluacion_max/cuestionario.preguntas.length*100"
                          color="#cfcb5f"
                        >{{cuestionario.evaluacion_max}}/{{cuestionario.preguntas.length}}</v-progress-circular>
                      </div>
                    </v-card>
                  </v-col>
                  <v-col v-if="cuestionarios_loading" cols="12" sm="6">
                    <div class="cartel-loading text-center">
                      <v-progress-circular :size="30" color="#cfcb5f" indeterminate></v-progress-circular>
                    </div>
                  </v-col>
                </v-row>
              </v-container>
            </div>
          </v-tab-item>
        </v-tabs-items>
      </v-col>
    </v-row>
  </v-container>
</template>

<script>
import Avatar from "../Avatar.vue";
import Conocimiento from "../../models/Conocimiento";
export default {
  data() {
    return {
      items: [
        {
          nombre: "texto",
          respuesta: ""
        },
        {
          nombre: "importancia",
          respuesta: ""
        },
        {
          nombre: "ejemplos",
          respuesta: "Te muestro algunos ejemplos de @."
        },
        {
          nombre: "quiz",
          respuesta: "Te muestro algunos ejercicios de @."
        },
        {
          nombre: "imagen",
          respuesta: "Te muestro esta imagen de @."
        },
        {
          nombre: "video",
          respuesta: "Te muestro este video de @."
        },
        {
          nombre: "documento",
          respuesta: "Te muestro este documento de @."
        },
        {
          nombre: "explicacion",
          respuesta: "Aquí puedes conseguir más información de @."
        },
        {
          nombre: "faq",
          respuesta: ""
        }
      ],
      mensajes: [
        new Mensaje(0, "Soy tu docente virtual. Prueba escribiéndome algo.")
      ],
      materiales: [],
      conocimientos: [],
      cuestionarios: [],
      cuestionario_tiempo: 0,
      cuestionario_cronometro: 0,
      material_seleccionado: null,
      item_seleccionado: "",
      item_seleccionado_idx: 0,
      prioridad: [],
      cuestionario_seleccionado: null,
      p_idx: 0,
      puntaje: 0,
      puntaje_aux: 0,
      //Loadings
      materiales_loading: true,
      cuestionarios_loading: true,
      microfono_loading: false,
      enviar_mensaje: true,
      mostrando_correcta: false,
      mostrando_correcta_tiempo: 1000,
      mostrando_puntaje: false,
      //Auxiliars
      tab: 0,
      c_tab: 0,
      texto: "",
      //Avatar
      avatar_component: null,
      emotion_time: 1500
    };
  },
  mounted() {
    this.avatar_component = this.$refs["avatar"];
    this.tema_id = this.$route.params["tema_id"];
    this.sesion = this.$store.state.servicio.sesion.obtenerSesion();
    this.scrollDown();
    this.obtenerMateriales();
    this.obtenerCuestionarioPorTema();
  },
  methods: {
    seleccionarMaterial(material_id, item = "") {
      let mostrar_material = true;
      let obtenerMaterial =
        this.sesion.tipo == "profesor" || item
          ? this.$store.state.servicio.material.obtenerMaterialPorId
          : this.$store.state.servicio.material.obtenerMaterialPorAlumnoId;

      obtenerMaterial(
        this.sesion.id,
        material_id,
        res => {
          if (item) {
            if (["importancia", "texto"].includes(item)) {
              this.agregarMensajeAnimado(res[item]);
              mostrar_material = false;
            } else {
              let respuesta = this.items
                .find(i => i["nombre"] == item)
                ["respuesta"].replace(/@/, res["nombre"]);
              this.agregarMensajeAnimado(respuesta);
              this.prioridad = [{ item: item }];
            }
          } else if (res["prioridad"]) {
            this.prioridad = res["prioridad"];
            this.prioridad.sort((a, b) => b.puntaje - a.puntaje);
          } else {
            this.prioridad = this.items.map(i => {
              let aux = {
                item: i["nombre"]
              };
              return aux;
            });
          }
          if (mostrar_material) {
            this.tab = 0;
            this.material_seleccionado = res;
            this.seleccionarItem(0);
          }
        },
        error => {
          console.log(error);
        }
      );
    },
    seleccionarItem(i_idx) {
      i_idx = Math.max(0, Math.min(this.prioridad.length - 1, i_idx));
      this.item_seleccionado_idx = i_idx;
      this.item_seleccionado = this.prioridad[i_idx]["item"];
    },
    obtenerCuestionarioPorTema() {
      this.$store.state.servicio.cuestionario.obtenerCuestionarioPorTema(
        this.tema_id,
        res => {
          this.cuestionarios_loading = false;
          this.cuestionarios = res.map(
            c =>
              new Cuestionario(
                c.cuestionario_id,
                c.nombre,
                c.preguntas,
                c.nivel,
                c.tiempo
              )
          );
          this.obtenerEvaluaciones();
        },
        error => {
          this.cuestionarios_loading = false;
          console.log(error);
        }
      );
    },
    actualizarEvaluacion(cuestionario_id, nota) {
      this.$store.state.servicio.evaluacion.actualizarEvaluacion(
        this.sesion["id"],
        cuestionario_id,
        this.obtenerFechaActual(),
        nota,
        res => {
          this.obtenerEvaluaciones();
        },
        err => {}
      );
    },
    obtenerEvaluaciones() {
      this.cuestionarios.forEach(c => {
        this.$store.state.servicio.evaluacion.obtenerEvaluacion(
          this.sesion["id"],
          c.id,
          res => {
            c.evaluaciones = res;
            if (res.length)
              c.evaluacion_max = Math.max(...res.map(e => e.nota));
          },
          err => {
            console.log(err);
          }
        );
      });
    },
    obtenerMateriales() {
      this.$store.state.servicio.material.obtenerMaterialPorTema(
        this.$route.params["tema_id"],
        res => {
          this.materiales_loading = false;
          this.materiales = JSON.parse(res);
          this.obtenerConocimientos();
        },
        error => {
          this.materiales_loading = false;
          console.log(error);
        }
      );
    },
    seleccionarCuestionario(c_idx) {
      this.cuestionario_seleccionado = Object.assign(
        {},
        this.cuestionarios[c_idx]
      );
      // Reiniciando valores
      this.p_idx = 0;
      this.puntaje = 0;
      this.cuestionario_tiempo = this.cuestionario_seleccionado["tiempo"];
      this.iniciarCronometro();
    },
    iniciarCronometro() {
      clearInterval(this.cuestionario_cronometro);
      this.cuestionario_cronometro = setInterval(() => {
        this.cuestionario_tiempo--;
        if (this.cuestionario_tiempo <= 0) {
          this.finalizarCuestionario();
        }
      }, 1000);
    },
    finalizarCuestionario() {
      clearInterval(this.cuestionario_cronometro);
      if (this.cuestionario_seleccionado) {
        let n_preguntas = this.cuestionario_seleccionado.preguntas.length;
        this.mostrando_puntaje = true;
        this.puntaje_aux = 0;

        this.actualizarEvaluacion(
          this.cuestionario_seleccionado["id"],
          this.puntaje
        );
        setTimeout(() => {
          this.puntaje_aux = this.puntaje;
        }, 200);

        if (this.puntaje == 0) {
          // this.avatar_component.showEmotion("ANG", this.emotion_time); // Un poco fuerte xd
        } else if (this.puntaje == n_preguntas) {
          this.avatar_component.showEmotion("LOV", this.emotion_time);
          this.agregarMensaje(0, `Excelente.`, () => {});
        } else {
          this.agregarMensaje(
            0,
            `Acertaste ${this.puntaje} de ${n_preguntas} preguntas.`,
            () => {}
          );
        }
      }
    },
    seleccionarAlternativaQuiz(q_idx, a_idx) {
      if (!this.material_seleccionado.quiz[q_idx].mostrando_correcta) {
        this.material_seleccionado.quiz[q_idx].mostrando_correcta = true;
        if (this.material_seleccionado.quiz[q_idx].correcta == a_idx) {
          // this.agregarMensaje(0, "Respuesta correcta.", () => {});
          this.TextToSpeech("Respuesta correcta.", () => {});
          this.avatar_component.showEmotion("HAP", this.emotion_time);
        } else {
          // this.agregarMensaje(0, "Respuesta incorrecta.", () => {});
          this.TextToSpeech("Respuesta incorrecta.", () => {});
          this.avatar_component.showEmotion("SAD", this.emotion_time);
        }
        this.$forceUpdate(); /* Corrigiendo actualizacion binding */
      }
    },
    seleccionarAlternativa(a_idx) {
      // Poder seleccionar alternativa si acabó la animación
      if (!this.mostrando_correcta) {
        // Mostrar animación
        this.mostrando_correcta = true;
        let n_preguntas = this.cuestionario_seleccionado.preguntas.length;
        if (
          this.cuestionario_seleccionado.preguntas[this.p_idx].correcta == a_idx
        ) {
          this.puntaje++;
          // this.agregarMensaje(0, "Respuesta correcta.", () => {});
          this.TextToSpeech("Respuesta correcta.", () => {});
          this.avatar_component.showEmotion("HAP", this.emotion_time);
        } else {
          // this.agregarMensaje(0, "Respuesta incorrecta.", () => {});
          this.TextToSpeech("Respuesta incorrecta.", () => {});
          this.avatar_component.showEmotion("SAD", this.emotion_time);
        }
        setTimeout(() => {
          // Animación terminada y mostrar siguiente pregunta o resultado
          if (this.p_idx < n_preguntas - 1) {
            this.p_idx++;
          } else {
            this.finalizarCuestionario();
          }
          this.mostrando_correcta = false;
        }, this.mostrando_correcta_tiempo);
      }
    },
    enviarMensaje() {
      if (this.texto.trim().length != 0 && this.enviar_mensaje) {
        this.enviar_mensaje = false;
        this.mensajes.push(new Mensaje(1, this.texto));

        let tema_id = this.$route.params["tema_id"];
        let consulta = this.texto;
        this.texto = "";

        this.obtenerRespuesta(tema_id, consulta);
        this.scrollDown();
      }
    },
    agregarMensaje(tipo, mensaje, onend) {
      this.mensajes.push(new Mensaje(tipo, mensaje));
      this.TextToSpeech(mensaje, onend);
      this.scrollDown();
    },
    agregarMensajeAnimado(mensaje) {
      setTimeout(() => {
        this.avatar_component.setAnimationTalk(2147483647);
      }, 250);
      this.agregarMensaje(0, mensaje, event => {
        this.avatar_component.stopAnimation();
        this.avatar_component.setAnimationNormal();
      });
    },
    obtenerRespuesta(tema_id, consulta) {
      let obtenerRespuesta =
        this.sesion.tipo == "profesor"
          ? this.$store.state.servicio.chat.obtenerRespuestaProfesor
          : this.$store.state.servicio.chat.obtenerRespuestaAlumno;
      let obtenerMaterial =
        this.sesion.tipo == "profesor"
          ? this.$store.state.servicio.material.obtenerMaterialPorId
          : this.$store.state.servicio.material.obtenerMaterialPorAlumnoId;

      obtenerRespuesta(
        tema_id,
        consulta,
        this.sesion.id,
        res => {
          //onSuccess
          this.enviar_mensaje = true;

          if (res) {
            if (res["respuesta"]) {
              this.agregarMensajeAnimado(res["respuesta"]);
            }
            if (res["material_id"]) {
              this.seleccionarMaterial(
                res["material_id"],
                res["respuesta_item"]
              );
            }
          }
        },
        error => {
          //onError
          this.enviar_mensaje = true;
          this.scrollDown();
          console.log(error);
        }
      );
    },
    scrollDown() {
      var objDiv = document.getElementById("chat-ventana");
      setTimeout(() => {
        // if (objDiv.scrollHeight - objDiv.scrollTop < 1200)
        objDiv.scrollTop = objDiv.scrollHeight;
      }, 0);
    },
    obtenerPreguntaMaterial() {
      this.$store.state.servicio.obtenerPreguntaMaterial(
        res => {
          console.log(res);
        },
        err => {}
      );
    },
    obtenerConocimientos() {
      this.$store.state.servicio.obtenerPreguntaMaterial(
        res => {
          let plantillas = res;
          this.$store.state.servicio.conocimiento.obtenerConocimientoPorTema(
            this.tema_id,
            res => {
              this.conocimientos = JSON.parse(res);
              this.materiales.forEach(m => {
                this.items.forEach(i => {
                  if (i["nombre"] != "faq") {
                    let pregunta = plantillas[i["nombre"]].replace(
                      /@/,
                      m["nombre"]
                    );
                    this.conocimientos.push(
                      new Conocimiento("", "", [pregunta], "")
                    );
                  } else {
                    m["faq"].forEach(f => {
                      this.conocimientos.push(
                        new Conocimiento("", "", [f["pregunta"]], "")
                      );
                    });
                  }
                });
              });
            },
            err => {
              console.log(err);
            }
          );
        },
        err => {
          console.log(err);
        }
      );
    },
    escribirMensaje(mensaje) {
      this.texto = mensaje;
    },
    hablarMensaje() {
      this.microfono_loading = true;
      setTimeout(() => {
        this.microfono_loading = false;
      }, 10000);
      this.SpeechToText(text => {
        this.microfono_loading = false;
        this.texto = text;
        this.enviarMensaje();
      });
    },
    TextToSpeech(text, onend) {
      if ("speechSynthesis" in window) {
        var synth = window.speechSynthesis;
        synth.cancel();
        var utterance = new SpeechSynthesisUtterance(text);
        utterance.rate = 1;
        utterance.pitch = 1.5;
        utterance.lang = "es-ES";
        utterance.onend = onend;
        synth.speak(utterance);
      }
    },
    SpeechToText(onresult) {
      var SpeechRecognition = SpeechRecognition || webkitSpeechRecognition;
      var recognition = new SpeechRecognition();
      recognition.start();
      recognition.onresult = function(event) {
        var last = event.results.length - 1;
        var text = event.results[last][0].transcript;
        onresult(text);
      };
    },
    obtenerFechaActual() {
      var today = new Date();
      var dd = today.getDate();
      var mm = today.getMonth() + 1; //January is 0!
      var yyyy = today.getFullYear();

      if (dd < 10) {
        dd = "0" + dd;
      }
      if (mm < 10) {
        mm = "0" + mm;
      }
      return dd + "/" + mm + "/" + yyyy;
    }
  },
  components: {
    Avatar
  }
};
class Mensaje {
  constructor(autor, mensaje, texto, video) {
    this.autor = autor;
    this.mensaje = mensaje;
    this.texto = texto;
    this.video = video;
  }
}
class Cuestionario {
  constructor(id, nombre, preguntas, nivel, tiempo) {
    this.id = id;
    this.nombre = nombre;
    this.preguntas = preguntas;
    this.evaluaciones = [];
    this.evaluacion_max = 0;
    this.nivel = nivel;
    this.tiempo = tiempo;
  }
}
class Pregunta {
  constructor(enunciado, alternativas, correcta) {
    this.enunciado = enunciado;
    this.alternativas = alternativas;
    this.correcta = correcta;
  }
}
</script>

